import React, { useEffect } from 'react';
import './AboutMe.css'
import slide1 from '../assets/Slide1.png';
import slide2 from '../assets/Slide2.png';
import slide3 from '../assets/Slide3.png';
import slide4 from '../assets/Slide4.png';
import slide5 from '../assets/Slide5.png';
import slide6 from '../assets/Slide6.png';
import slide7 from '../assets/Slide7.png';
import slide8 from '../assets/Slide8.png';

import hrtLogo from '../assets/hrt_logo.png'

import { Pagination } from 'swiper';
import SwiperCore, { Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';

SwiperCore.use([Autoplay]);

const data = [
  {
    avatar: slide1,
    review: 'Cuc thi Vietnamese Youth Technopreneur Challenge 2022 - VYTEC22'
  },
  {
    avatar: slide2,
    review: 'Cuc thi Vietnamese Youth Technopreneur Challenge 2021 - VYTEC21'
  },
  {
    avatar: slide3,
    review: 'Cuc thi  tng sng to cng ngh'
  },
  {
    avatar: slide4,
    name: 'Nana McBrown',
    review: 'Cuc thi m mm  tng sng to khi nghip cng ngh - Innovative Entrepreneurs 2019'
  },
  {
    avatar: slide5,
    review: 'Ngy hi truyn thng UET Connect'
  },
  {
    avatar: slide6,
    review: 'Cuc thi UET Makerthon'
  },
  {
    avatar: slide7,
    review: 'Ngy hi vic lm UET Job Fair'
  },
  {
    avatar: slide8,
    review: 'Cuc thi UET HACKATHON OPEN 2022'
  },
  
]
export const AboutMe = () => {
  

  return (
    <section id='testimonials'>
    <h1>V CHNG TI</h1>
    <img src={hrtLogo} id='hrtlogo'></img>
    <h1>CU LC B HRTECH</h1>

    <Swiper className="container testimonials__container"
    // install Swiper modules
    modules={[Pagination]}
    spaceBetween={100}
    slidesPerView={1}
    pagination={{ clickable: true }}
    autoplay={{ delay: 2000, disableOnInteraction: false  }}
    >
      {
        data.map(({avatar, name, review}, index) => {
          return (
            <SwiperSlide key={index} className='testimonial'>
              <div className="client__avatar">
                <img src={avatar}/>       
              </div>
             
                <span className='client__review'>
                  {review}
                </span>
            </SwiperSlide>
          )
        })
      }  
    </Swiper>
  </section>
  )
}